input {
  margin: 3px;
  width: 40px;
}

div,
p {
  box-sizing: border-box;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 10px;
}

.content {
  padding: 20px;
  text-align: left;
}

.result {
  padding: 10px;
  border-radius: 10px;
  background-color: #D5DCEE;
  width: 150px;
}